.sb-example-3 .search__title {
  font-size: 22px;
  font-weight: 900;
  text-align: center;
  color: #4da8da;
}

.sb-example-3 .search__input {
  padding: 12px 24px;
  width: 30rem;
  background-color: transparent;
  transition: -webkit-transform 250ms ease-in-out;
  transition: transform 250ms ease-in-out;
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  font-size: 1rem;
  line-height: 18px;
  background-color: transparent;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: 95% center;
  border-radius: 50px;
  border: 1px solid #4da8da;
  transition: all 250ms ease-in-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  color: #4da8da;
}

.sb-example-3 .search__input::-webkit-input-placeholder {
  color: #4da8da;
  letter-spacing: 1.5px;
}

.sb-example-3 .search__input:-ms-input-placeholder {
  color: #4da8da;
  letter-spacing: 1.5px;
}

.sb-example-3 .search__input::placeholder {
  color: #4da8da;
  letter-spacing: 1.5px;
}

.sb-example-3 .search__input:hover,
.search__input:focus {
  padding: 12px 0;
  outline: 0;
  border: 1px solid transparent;
  border-bottom: 1px solid #4da8da;
  border-radius: 0;
  background-position: 100% center;
}

